:root {
  --main-bg-color: #fefefe;
  --main-text-color: #2c2c2c;
  --menu-text-color: #000;
  --hover-green-color: #bbf330;
  --hover-btn1-color: #b5ff00;
  --main-yellow-color: #ffe550;
  --hover-arrow-color: #7eb101;
  --hover-btn-color: #597d00;
  --font: "Roboto", sans-serif;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: #000;
  text-decoration: none;
  display: block;
}

.header__container {
  background-color: var(--main-bg-color);
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: relative;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.nav__link {
  font-family: var(--font);
  color: var(--menu-text-color);
  font-weight: 400;
}

@media screen and (max-width: 767px) {
  .nav {
    z-index: 4;
    width: 130px;
    opacity: 0;
    pointer-events: none;
    transition: opacity var(--animation);
    background: #fefefe;
    border-radius: 30px 0 30px 30px;
    position: absolute;
    top: 50px;
    right: 20px;
    overflow: hidden;
    box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
  }

  .nav__link {
    width: 100%;
    height: 100%;
    transition: background-color var(--animation);
    padding: 16px 10px 16px 16px;
    font-size: 16px;
    line-height: 1.1875;
  }

  .nav__link:hover, .nav__link:focus {
    background-color: var(--hover-green-color);
  }

  .btn__header {
    display: none;
  }

  .btn-mobile-menu {
    color: var(--hover-arrow-color);
    cursor: pointer;
    transition: color var(--animation);
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    justify-content: center;
    align-items: center;
    padding: 0;
    display: flex;
  }

  .btn-mobile-menu__close {
    position: absolute;
    top: 4px;
    right: 4px;
  }

  .btn-mobile-menu:hover, .btn-mobile-menu:focus {
    color: var(--main-yellow-color);
  }

  .btn-mobile-menu__icon {
    fill: currentColor;
  }

  .is-open {
    opacity: 1;
    pointer-events: initial;
  }
}

@media screen and (min-width: 768px) {
  .nav, .nav__list {
    align-items: center;
    gap: 28px;
    display: flex;
  }

  .nav__link {
    transition: color var(--animation);
    font-size: 14px;
    line-height: 1.14286;
  }

  .nav__link:hover, .nav__link:focus {
    color: var(--hover-green-color);
    text-decoration: underline;
  }

  .btn__header {
    width: 100px;
    height: 32px;
    font-family: var(--font);
    background-color: var(--hover-green-color);
    cursor: pointer;
    transition: background-color var(--animation);
    border: none;
    border-radius: 30px;
    padding: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    display: block;
  }

  .btn__header:hover, .btn__header:focus {
    background-color: var(--main-yellow-color);
  }

  .btn-mobile-menu {
    display: none;
  }
}

